import React from 'react';
import { Avatar, Button, makeStyles, Tooltip } from '@fluentui/react-components';
import { getAllNames } from './names';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'start',
    gap: '5px',
  },

  list: {
    display: 'flex',
    gap: '5px',
    flexWrap: 'wrap',
  },
});

let allNames: string[];

const randomNames = () => {
  if (!allNames) {
    allNames = getAllNames();
  }

  const count = 2000;

  for (let i = 0; i < count; i++) {
    const j = Math.floor(Math.random() * (allNames.length - i)) + i;
    [allNames[i], allNames[j]] = [allNames[j], allNames[i]];
  }

  return allNames.slice(0, count);
};

export const AvatarList: React.FC = () => {
  const [names, setNames] = React.useState(randomNames);

  const classes = useStyles();

  return (
    <div className={classes.root}>
      <span>Sample of {names.length} random names:</span>
      <Button onClick={() => setNames(randomNames())}>Shuffle</Button>
      <div className={classes.list}>
        {names.map((name) => (
          <Tooltip key={name} content={name}>
            <Avatar name={name} color="colorful" />
          </Tooltip>
        ))}
      </div>
    </div>
  );
};
