import React from 'react';
import './styles.css';
import { makeStyles, FluentProvider, webLightTheme } from '@fluentui/react-components';
import { ColorBarChart } from './ColorBarChart';
import { AvatarList } from './AvatarList';

const useStyles = makeStyles({
  root: {
    padding: '10px',
    display: 'grid',
    gridTemplate: 'auto / 250px 1fr',
    gap: '20px',
  },
});

export default function App() {
  const classes = useStyles();

  return (
    <FluentProvider theme={webLightTheme} className={classes.root}>
      <ColorBarChart />
      <AvatarList />
    </FluentProvider>
  );
}
