import React from 'react';
import { makeStyles, mergeClasses } from '@fluentui/react-components';
import { avatarColors, useAvatarColorStyles } from './avatarColors';
import { getHashCode } from './getHashCode';
import { getAllNames } from './names';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
    gap: '1px',
  },

  item: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    borderStyle: 'solid',
    boxSizing: 'border-box',
    padding: '5px',
    borderWidth: '0 1px 0 0',
  },
});

const calculateStats = () => {
  const names = getAllNames();
  const counts = avatarColors.map(() => 0);
  let max = 0;
  names.forEach((name) => {
    const colorIndex = getHashCode(name) % avatarColors.length;
    const count = ++counts[colorIndex];
    if (count > max) {
      max = count;
    }
  });
  return { counts, total: names.length, max };
};

export const ColorBarChart: React.FC = () => {
  const [{ counts, total, max }] = React.useState(calculateStats);
  const classes = useStyles();
  const colorClasses = useAvatarColorStyles();

  return (
    <div className={classes.root}>
      Statistics for {total} names:
      {avatarColors.map((color, colorIndex) => {
        const className = mergeClasses(classes.item, colorClasses[color]);
        return (
          <div key={color} className={className} style={{ width: `${(100 * counts[colorIndex]) / max}%` }}>
            {color} ({Math.round((1000 * counts[colorIndex]) / total) / 10}%)
          </div>
        );
      })}
    </div>
  );
};
